"use client"; // Required to use usePathname

import Link from 'next/link';
import { usePathname } from 'next/navigation';
import { LayoutDashboard, MessageSquare, Settings, PlusCircle } from 'lucide-react';
import { clsx } from 'clsx'; // Optional helper for cleaner classes

export function Sidebar() {
  const pathname = usePathname();

  const navItems = [
    { name: "Chat", href: "/", icon: MessageSquare },
    { name: "Agents List", href: "/api/agents", icon: LayoutDashboard },
    { name: "New Agent", href: "/api/create", icon: PlusCircle },
    { name: "Settings", href: "/api/settings", icon: Settings },
  ];

  return (
    <div className="w-64 bg-zinc-900 border-r border-zinc-800 flex flex-col p-4 gap-2">
      <div className="flex items-center gap-2 px-2 mb-8 text-white font-bold text-lg">
        <div className="h-8 w-8 bg-blue-600 rounded-lg flex items-center justify-center">A</div>
        AgentCenter
      </div>

      <nav className="flex-1 flex flex-col gap-1">
        {navItems.map((item) => {
          const isActive = pathname === item.href;
          return (
            <Link
              key={item.name}
              href={item.href}
              className={clsx(
                "flex items-center gap-3 px-3 py-2 rounded-lg transition-all",
                isActive 
                  ? "bg-blue-600/10 text-blue-500 font-medium" 
                  : "text-zinc-400 hover:bg-zinc-800 hover:text-white"
              )}
            >
              <item.icon size={18} />
              {item.name}
            </Link>
          );
        })}
      </nav>
    </div>
  );
}